.form-landing-information-container {
  position: relative;
  z-index: 1;
  margin: 40px 0; }
  @media screen and (max-width: 576px) {
    .form-landing-information-container {
      max-width: 350px;
      margin-left: auto;
      margin-right: auto; } }
  @media screen and (min-width: 769px) {
    .form-landing-information-container {
      margin: 40px 0; } }
  .form-landing-information-container h2 {
    text-align: center; }
  .form-landing-information-container .main-title {
    font-weight: 800;
    font-size: 120px;
    mix-blend-mode: multiply;
    color: rgba(231, 235, 253, 0.397); }
    @media screen and (max-width: 992px) {
      .form-landing-information-container .main-title {
        font-size: 96px; } }
    @media screen and (max-width: 768px) {
      .form-landing-information-container .main-title {
        display: none; } }
  .form-landing-information-container .sub-title {
    margin-top: -14px;
    transform: translateY(-50px); }
    @media screen and (max-width: 576px) {
      .form-landing-information-container .sub-title {
        margin-left: 5vw; } }
    @media screen and (max-width: 768px) {
      .form-landing-information-container .sub-title {
        margin-top: 0;
        transform: none;
        max-width: 183px;
        text-align: left;
        margin-left: 15px; } }
    @media screen and (min-width: 769px) {
      .form-landing-information-container .sub-title {
        color: #354F53 !important; } }
    .form-landing-information-container .sub-title.center {
      text-align: center;
      max-width: none;
      margin-left: 0px; }
  .form-landing-information-container .information-container {
    display: flex;
    justify-content: center;
    align-items: stretch;
    flex-wrap: wrap; }
    .form-landing-information-container .information-container .desc-container {
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      text-align: center;
      padding: 0 10px; }
      @media screen and (max-width: 576px) {
        .form-landing-information-container .information-container .desc-container {
          width: 50% !important; } }
      .form-landing-information-container .information-container .desc-container .img-container {
        width: 160px;
        height: 160px;
        background: white;
        border-radius: 50%;
        /* Mild shadow */
        box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.05);
        display: flex;
        justify-content: center;
        align-items: center; }
        .form-landing-information-container .information-container .desc-container .img-container img {
          width: 50%;
          height: 50%; }
        @media screen and (max-width: 992px) {
          .form-landing-information-container .information-container .desc-container .img-container {
            width: 130px;
            height: 130px; } }
        @media screen and (max-width: 768px) {
          .form-landing-information-container .information-container .desc-container .img-container {
            width: 100px;
            height: 100px; } }
      .form-landing-information-container .information-container .desc-container h3 {
        min-height: 42px;
        margin-top: 13px;
        margin-bottom: 19px; }
        @media screen and (max-width: 991px) {
          .form-landing-information-container .information-container .desc-container h3 {
            min-height: 32px; } }
        @media screen and (max-width: 576px) {
          .form-landing-information-container .information-container .desc-container h3 {
            min-height: 0; } }
      .form-landing-information-container .information-container .desc-container h5 {
        max-width: 200px;
        text-align: left; }
        @media screen and (max-width: 768px) {
          .form-landing-information-container .information-container .desc-container h5 {
            max-width: 120px; } }

.landing-green-style .main-title {
  color: #F2FDFB; }

.landing-blue-style .main-title {
  color: rgba(231, 235, 253, 0.397); }
